img, frame {
  height: auto;
  margin: auto;
  max-width: 100%;
}

ol, ul {
  margin-bottom: $base-line-height;
  margin-top: 0;
}


.dl-list {
  margin-bottom: $base-line-height;

  dt {
    color: $gray-darker;
    float: left;
    margin-right: em($base-line-height);
  }

  dd {
    display: block;
    margin: 0 0 $base-line-height*0.25 $base-line-height*7;
  }
}

.clear {
  clear: both;
}

.clearfix {
  *zoom: 1;
}

.clearfix:after {
  content: "\0020";
  display: block;
  height: 0;
  clear: both;
  overflow: hidden;
  visibility: hidden;
}

hr {
  border-color: $gray-light;
}

.v_separate {
  margin-bottom: $base-line-height*2;
  margin-top: $base-line-height*2;
}

.list-unstyled {
  list-style: none;
  padding-left: 0;
}

.vcenter {
 @include  vertical-center;
}

.justify {
 @include  blocks-justify;
}

.outpage {
  height: 1px;
  left: -10000px;
  position: absolute;
  width: 1px;
}
  
.back0,
#smartFilter .lvl1_specific_size .lvl2 .props.pchecked,
#smartFilter .lvl1_price_group .lvl2 .props.pchecked,
.bx_filter .rs_prop_btn.active {
  @extend %theme-background0;
}
.back0_inv,
#smartFilter .lvl1_specific_size .lvl2 .props.pchecked:hover,
#smartFilter .lvl1_price_group .lvl2 .props.pchecked:hover,
.bx_filter .rs_prop_btn.active:hover {
  @extend %theme-background0-inv;
}

.back1 {
  @extend %theme-background4;
}

.back2 {
  @extend %theme-background4;
}

.back2_inv {
  @extend %theme-background4-inv;
}